body {
  font-family: 'Segoe UI', sans-serif;
  background: #f0f4f8;
  padding: 20px;
  display: flex;
  justify-content: center;
}

.container {
  background: white;
  padding: 30px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  text-align: center;
}

h1 {
  margin-bottom: 30px;
}

.question {
  margin-bottom: 25px;
  text-align: left;
}

.question label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

.options {
  display: flex;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
}

.option-btn {
  padding: 8px 12px;
  border: 1px solid #ccc;
  background: #f5f5f5;
  border-radius: 5px;
  cursor: pointer;
  transition: 0.2s;
}

.option-btn:hover {
  background: #e0e0e0;
}

.option-btn.selected {
  background: #0078d4;
  color: white;
  border-color: #0078d4;
}

.submit-btn {
  margin-top: 20px;
  padding: 10px 20px;
  background: #0078d4;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

#result {
  margin-top: 30px;
  font-size: 1.2em;
}

