@media (max-width: 600px) {
  .container {
    padding: 20px;
    border-radius: 0;
    box-shadow: none;
  }

  .question {
    text-align: center;
  }

  .options {
    flex-direction: column;
    align-items: center;
  }

  .option-btn {
    width: 100%;
    max-width: 300px;
    margin-bottom: 8px;
  }

  .submit-btn {
    width: 100%;
    max-width: 300px;
  }
}